import React, { Component } from 'react';
import '../../bower_components/bootstrap/dist/css/bootstrap.css';
import '../../styles/main.css';
import '../../styles/custom.css';
import Img from 'react-image';
import insyt_icon from '../../icons/insyt_logo.png';
import thumb_icon from '../../icons/thumb.svg';
import thumb_login_icon from '../../icons/fingerprint.svg';
import {Link} from 'react-router-dom';

import SignupStore from '../../stores/SignupStore';
import * as SignupAction from '../../actions/SignupAction';
import _ from 'lodash';

import * as utils from '../../utils/utils';
import cookie from 'react-cookies';
import ReactSVG from 'react-svg';

class CStageOne extends Component {

    constructor(){
        super();
        
        this.getUser = this.getUser.bind(this);

        this.cNameErrorText = 'Please Enter Corporate Name';
        this.lNameErrorText = 'Please Enter Location';

        this.cName = '';
        this.lName = '';
        this.user = SignupStore.getUser();
        this.nextButtonText = 'Next';

        this.state = {    
           
        }
    }

    componentWillMount(){
                
        this.clearCookies();
    }

    componentWillUnMount(){
        
    }

    getUser(){
        this.setState({
            user : SignupStore.getUser()
        });
    }

    cNameExists(){
        this.cNameErrorText = 'Corporate Name Already Exist';
        
        this.setState({
            cNameError : true
        })

        this.disableButton(false, 'Next');
    }

    cNameNotExists(){
        this.cNameErrorText = '';
              
    }

    handleBrowserConfig(){
        cookie.save('cname', this.cname);
        cookie.save('clocation', this.clocation);
    }

    onNextClicked(){
        if(this.doValidate()){
            //Set up user
            this.disableButton(true, 'Please Wait ...');

            this.grabDetails();
            this.handleBrowserConfig();
            SignupAction.validateEmail(this.user);
        }
    }

    disableButton(isDisable, label){
        this.setState({
            signupButtonText : label,
            isButtonDisabled : isDisable
        });
    }

    grabDetails(){
        this.user.cname = this.cname.trim();
        this.user.lname = this.lname.trim();
    }

    doValidate(){
        if(this.lname){
        }


        return true;
    }

    clearCookies(){
       
    }

    render() {
        return (
            <div className="signup">
                <div className="ad col-md-8 hidden-sm hidden-xs">
                    <div>
                        <Img src={insyt_icon} className="icon" />
                    </div>
                    <div className="col-md-12">
                        <ReactSVG path={thumb_icon} callback={svg => {}} className="thumb"/>
                    </div>
                </div>

                <div className="control col-md-4 col-sm-12 col-xs-12">
                    <div className="sign-in-wrapper">
                        <div className="sign-container">
                            <div className="text-center">
                                <h2 className="logo">
                                    <ReactSVG path={thumb_login_icon} callback={svg => {}} className="thumb"/>
                                </h2>
                                <h4 className="typo-style">Corporate</h4>
                            </div>

                            <div className="sign-in-form">
                                <div className="form-group">
                                    <input type="text" className="form-control" placeholder="Corporate Name" value={this.state.msisdn} onChange={this.onMsisdnChanged.bind(this)}/>
                                    <span className={this.state.mError ? 'error' : 'vamus'}>{this.msisdnErrorText}</span>
                                </div>
                                <div className="form-group">
                                    <input type="password" className="form-control" placeholder="Location" value={this.state.password} onChange={this.onPasswordChanged.bind(this)}/>
                                    <span className={this.state.pError ? 'error' : 'vamus'}>{this.passwordErrorText}</span>
                                </div>
                            
                                <button className="btn btn-info btn-block" onClick={this.onRegisterClicked.bind(this)} disabled={this.state.isButtonDisabled}>{this.nextButtonText}</button>
                                <br/>
                            
                                <Link to="/login" className="typo-style"><a className="btn btn-md btn-default btn-block typo-style">Go Back to Login</a></Link>
                            </div>
                            <div className="text-center copyright-txt">
                                <small className="typo-style">Inflexions - Copyright © 2017</small>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default CStageOne;



